import fFooter from "@/components/footer/f-footer";
import { Loading } from "element-ui";
export default {
  name: "registeredSuccess",
  components: {
    fFooter
  },
  data() {
    var reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
    var validateEmail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Email cannot be empty."));
      } else if (!reg.test(value)) {
        callback(new Error("Please enter a valid email address."));
      } else {
        callback();
      }
    };
    return {
      logEmail: "",
      logPass: "",
      regUsername: "",
      regEmail: "",
      regPass: "",
      rememberState: false,
      validateForgotPasswordForm: {
        email: ""
      },
      rules: {
        email: [
          {
            validator: validateEmail,
            trigger: "change"
          }
        ]
      },
      emailSentSuccessfully: false
    };
  },
  mounted() {
    this.buryingPoint.pageView();
  },
  methods: {
    sendEmail(formName) {
      let thif = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          Loading.service({
            lock: true,
            text: "Sending Email",
            background: "rgba(0, 0, 0, 0.7)"
          });
          var { email } = this.validateForgotPasswordForm;
          this.$apis
            .lostPassword({ email })
            .then(res => {
              if (res.status == 1) {
                Loading.service().close();
                thif.emailSentSuccessfully = true;
              } else {
                Loading.service().close();
                thif.common.msgBox(thif, res.msg, 3);
              }
            })
            .catch(err => {
              Loading.service().close();
              console.log(err);
            });
        } else {
          return false;
        }
      });
    },
    cancel(){
      this.$router.go(-1);
    },
    shopNow() {
      this.$router.push({ path: "/" });
    }
  }
};
